export default {
	props: {
		checked: {
			type: Boolean,
			default: false
		},
		value: {
			type: String,
			default: ''
		},
		color:{
			type:String,
			default:'#0da7a5'
		}
	},
	computed:{
		borderColor(){
			return `border: 2px solid ${this.color} `;
		},
		backgroundColor(){
			return `background-color:${this.color}`;
		},
	},
	methods: {
		tap(e) {
			e.value = e.currentTarget.dataset.value;
			this.$emit('check', e);
		}
	}
};
